import Vue from 'vue'
import Router from 'vue-router'
import WebIm from '@/components/WebIm'
import SkipWeb from '@/components/SkipWeb'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'WebIm',
      component: WebIm
    },{
    	path:'/webskip',
    	name:'SkipWeb',
    	component:SkipWeb
    }
  ]
})



