<template>
	<div>
		<van-swipe :autoplay="3000">
			<van-swipe-item v-for="img in images"><img :src="img" width="100%"/></van-swipe-item>
		</van-swipe>
		<van-tabs @click="onLoad">
			<van-tab v-for="good in goods" :title="good.title" style="font-size: 1rem;">
				<van-list v-model="loading" :finished="finished"  :offset="oof" class="js-markent-news">
					<div v-for="item in list" @click="skipH5(item.url)" class="warp">
					<van-row  style="background-color: #FFFFFF;" >
						<van-col span="16" style="position: relative;margin-right: 0.8rem;">
							<van-row style="text-align: left;color: #333;font-size:0.9648rem;font-size: 17.25px;line-height: 1.4472rem;font-weight: bold;">{{item.theme}}</van-row>
							<van-row style="text-align: left;font-family: '微软雅黑';font-size: 8%;color:#777777;font-size:1rem;line-height: 1.4472rem;padding-top: 0.8rem;">{{getNowFormatDate(item.createTime)}}</van-row>
						</van-col>
						<van-col span="6" style="padding:0"><img :src="item.thumbnail" width="120%" style="border-radius:5px;"/></van-col>
					</van-row>
					</div>
				</van-list>
			</van-tab>
		</van-tabs>
		
	</div>
</template>

<script>
	export default {
		name: 'WebIm',
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				page: 1,
				limit: 10,
				count: 0,
				flag: true,
				oof: 300,
				images: [
					'https://img.jinse.com/1088447',
					'https://img.jinse.com/1063912'
				],
				goods:[]
			}
		},
		methods: {
			onLoad(index,title) {
				var that = this;
					if(that.flag) {
						that.flag = false;
					} else {
						if(that.list.length >= that.count) {
							that.finished = true;
						}
					}
				that.list=[];

				that.$axios.get(that.BaseUrl+'news/list?page=' + that.page + '&limit=' + that.limit+'&category='+index)
					.then(function(res) {
						res = res.data;
						for(var i = 0; i < res.data.length; i++) {
							that.list.push(res.data[i]);
						}
						/*that.page = that.page + 1;*/
                        that.loading=false;
					})
					.catch(function(err) {

					})
				
			},
			skipH5:function(urlh5){
				var that =this;
				
				that.$router.push({
					name: 'SkipWeb',
					query:{
						un:encodeURI(urlh5)
					}
				});
			},
			getGoods: function() {
				this.goods = [{
						category:0,
						title: '链圈新闻'
					},
					{
						category:1,
						title: '公司文化'
					},
					{
						category:2,
						title: '社区节点'
					},
					{
						category:3,
						title: '理财专区'
					},
					{
						category:4,
						title: '战略合作'
					},
					{
						category:5,
						title: '团队风采'
					},
					{
						category:6,
						title: '商学院'
					}
				];
			},
		},
		mounted: function() {
			var that = this;
			that.getGoods();
		},
		created() {
            this.onLoad(0,'')
		}
	}
</script>

<style>
.van-row:after {
    clear: both;
    display: block;
    content: "";
    height: 0;
    line-height: 0;
    visibility: hidden;
}

.warp{
	padding: .5648rem 0;
    border-top: #F5F5F5 1px solid;
}
.js-markent-news {
    margin: 0 1.206rem 0 1.206rem;
    margin-bottom: 1.809rem;
}
</style>


