<template>
	<div>
		<van-nav-bar title="资讯详情" left-text="返回" left-arrow @click-left="onClickLeft" />
		<iframe :height="h" :src="un" width="100%"></iframe>
	</div>
</template>

<script>
	export default {
		name: "SkipWeb",
		data() {
			return {
				un: '',
				h:0
			};
		},
		mounted: function() {
			this.un = decodeURI(this.$route.query.un);
			this.h=window.innerHeight-60;
		},
		methods: {
			onClickLeft: function() {
				this.$router.push({
					name: 'WebIm'
				});
			}
		}
	}
</script>

<style>
</style>


